<template>
<v-expansion-panel style="width: 100%" @click="loadTargetData()">
    <v-expansion-panel-title>
        <v-row>
            <v-col cols="1" class="ma-0 px-0 text-right">
                <label >{{targetData.id}}</label>
            </v-col>
            <v-col  v-katex:auto>
                {{targetData.label}}
            </v-col>
            <v-col cols="1" v-if="targetKeys.includes('needed_question_ratio') && this.$store.state.datapagina_module.selected_tab == 'not_enough_questions'">
                <span>{{targetData.needed_questions}}</span><br>
                <span>Vragen Nodig</span>
            </v-col>
            <v-col cols="1">
            </v-col>
        </v-row>

    </v-expansion-panel-title>    
    <v-expansion-panel-text>
            <v-row v-if="targetKeys.includes('needed_question_ratio') && this.$store.state.datapagina_module.selected_tab == 'not_enough_questions'">
                <v-col>
                    <span>Ratio: </span><br>
                    <span>Needed Questions: </span><br>
                    <span>Has Questions: </span>
                </v-col>
                <v-col>
                    <span>{{Math.round(targetData.needed_question_ratio * 10000) / 10000}}</span><br>
                    <span>{{targetData.needed_questions}}</span><br>
                    <span>{{targetData.total_questions}}</span>
                </v-col>
            </v-row>
            <!-- if description is an string, show the string, if it's an obj sort and format the obj and show it nicer -->
                <v-expansion-panels>
                    <!-- description -->
                    <v-expansion-panel v-if="targetKeys.includes('description')">
                        <v-expansion-panel-title class="ma-0">
                            <p>Beschrijving</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <p class="box" >{{targetData.description}}</p>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- vraag ids -->
                    <v-expansion-panel v-if="targetKeys.includes('mcqs')">
                        <v-expansion-panel-title class="ma-0">
                            <p>Vraag Ids</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <p class="box" >{{targetData.mcqs.toString()}}</p>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Youtube video -->
                    <v-expansion-panel v-if="targetKeys.includes('youtube_id')">
                        <v-expansion-panel-title class="ma-0">
                            <p>Video</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text >
                            <iframe :src="'https://www.youtube.com/embed/' + targetData.youtube_id"  
                                    allow="accelerometer; 
                                            autoplay; 
                                            encrypted-media; 
                                            gyroscope; 
                                            picture-in-picture" 
                                    style="display: block; height:480px"
                                    width="100%"
                                    
                                    allowfullscreen></iframe>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Slide -->
                    <v-expansion-panel>
                        <v-expansion-panel-title class="ma-0">
                            <p>Slide</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <div v-if="targetKeys.includes('img_url_large')">
                                <img :src="targetData.img_url_large" alt="Target Photo" width="300">
                            </div>
                            <v-row  v-if="targetKeys.includes('slide_title')">
                                <v-col cols="2">
                                    <span class="pa-0 ma-0">Slide Titel: </span>
                                </v-col>
                                <v-col cols="10">
                                    <span class="pa-0 ma-0" v-katex:auto>{{targetData.slide_title}}</span>
                                </v-col>
                            </v-row>
                            <hr  v-if="targetKeys.includes('slide_title') && targetKeys.includes('slide_text')">
                            <v-row v-if="targetKeys.includes('slide_text')">
                                <v-col cols="2">
                                    <span class="pa-0 ma-0">Slide Text: </span>
                                </v-col>
                                <v-col cols="10">
                                    <span class="pa-0 ma-0"  v-katex:auto>{{targetData.slide_text}}</span>
                                </v-col>
                            </v-row>

                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Questions Data -->
                    <v-expansion-panel v-if="targetKeys.includes('mcqs')">
                        <v-expansion-panel-title class="ma-0">
                            <p>Vragen</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <v-expansion-panels>
                                <OutputListMcq v-for="(mcq_id, index) in targetData.mcqs" :key="index" :id="mcq_id" :no_target="true" />
                            </v-expansion-panels>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                    <!-- Tijd Data -->
                    <v-expansion-panel v-if="timeData != {}">
                        <v-expansion-panel-title class="ma-0">
                            <p>Tijd Data</p>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <v-table>
                                <thead>
                                    <tr>
                                        <th class="left-text">
                                            <p>Type</p>
                                        </th>
                                        <th class="left-text">
                                            <p>Data</p>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <!-- <div v-if="'time_estimate' in targetData.description"> -->
                                        <tr v-for="(key, index) in Object.keys(timeData)" :key="index">
                                            <td>{{key}}</td>
                                            <td>{{parseFloat(timeData[key]).toFixed(2)}}s</td>
                                        </tr>
                                    <!-- </div> -->
                                </tbody>
                            </v-table>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                </v-expansion-panels>

    </v-expansion-panel-text>
</v-expansion-panel>
</template>

<script>
import {defineAsyncComponent} from 'vue' 

export default {
    name: 'TargetPanel',
    props: {
        id: [Number, String],
    },
    components: {
        OutputListMcq: defineAsyncComponent(() => import('./McqPanel.vue'))
    },
    data (){
        return {
            loaded_data: false,
            refresh_target_data: 0
        }
    },
    computed: {
        targetData(){
            this.refresh_target_data;
            return this.$store.targetDataFromId(this.id)
        },
        targetKeys(){
            return Object.keys(this.targetData)
        },  
        mcqs(){
            return this.targetData.mcqs.map((e) => {return this.$store.getters.mcqDataFromId(e)})
        },
        timeData(){
            var time_data = {}
            this.mcqs.forEach((e) => {
                if (Object.keys(e).includes('time_estimate')){
                    Object.keys(e.time_estimate).forEach((time_key) => {
                        if (!Object.keys(time_data).includes(time_key)){
                            time_data[time_key] = 0
                        }

                        time_data[time_key] = time_data[time_key] + Math.round(e.time_estimate[time_key])
                    })
                }

            });
            return time_data
        }
    },
    methods: {
        loadTargetData: async function(){
            if (this.loaded_data == false){
                await this.$store.dispatch('loadTargetsFromArray', this.id)

                this.refresh_target_data++
                this.loaded_data = true
            }
        }
    },
    created() {
    },
}
</script>

<style>
.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    width: 100%;
    font-size: smaller;
    display: inline-block;
}
</style>