<template>
<v-expansion-panel style="width: 100%" @click="loadMcqData()">
    <v-expansion-panel-title>
        <v-row>
            <v-col cols="1" class="ma-0 px-0 text-right">
                <label >{{mcqData.id}}</label>
            </v-col>
            <v-col>
                {{mcqData.question_text}}
            </v-col>

        </v-row>

    </v-expansion-panel-title>    
    <v-expansion-panel-text>
        <v-expansion-panels>
            <!-- leerdoel id -->
            <v-expansion-panel>
                <v-expansion-panel-title class="ma-0">
                    <p>Leerdoel Id:</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <p class="box">{{mcqData.target_id}}</p>
                </v-expansion-panel-text >
            </v-expansion-panel>
            <!-- options -->
            <v-expansion-panel v-if="Object.keys(mcqData).includes('answer_options')">
                <v-expansion-panel-title class="ma-0">
                    <p>Keuzes:</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <v-table>
                        <thead>
                            <tr>
                                <th class="left-text">
                                    #
                                </th>
                                <th class="left-text">
                                    Option
                                </th>
                                <th class="left-text" v-if="Object.keys(mcqData).includes('answer_distribution')">
                                Aantal
                                </th>
                                <th class="left-text" v-if="Object.keys(mcqData).includes('answer_distribution')">
                                    %
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(option, index) in mcqData.answer_options" :key="index" :style="{'background-color': option.is_correct ? '#afff7d': 'white'}">
                                    <td>
                                        <span>{{option.id}}</span>
                                    </td>
                                    <td>{{option.text}}</td>
                                    <td v-if="Object.keys(mcqData).includes('answer_distribution')">
                                        <span>{{mcqData.answer_distribution[option.id]}}</span>
                                    </td>
                                    <td v-if="Object.keys(mcqData).includes('answer_distribution')">
                                        <span class="text-right">{{Math.round(mcqData.answer_distribution[option.id] / totalAnswerDistribution(mcqData.answer_distribution) * 100)}}</span>
                                    </td>
                            </tr>

                        </tbody>
                    </v-table>
                </v-expansion-panel-text >
            </v-expansion-panel>
            <!-- tijd data -->
            <v-expansion-panel v-if="Object.keys(mcqData).includes('time_estimate')">
                <v-expansion-panel-title class="ma-0">
                    <p>Tijd Data</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text>
                    <v-table>
                        <thead>
                            <tr>
                                <th class="left-text">
                                    <p>Type</p>
                                </th>
                                <th class="left-text">
                                    <p>Data</p>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <!-- <div v-if="'time_estimate' in mcqData.description"> -->
                                <tr v-for="(key, index) in Object.keys(mcqData.time_estimate)" :key="index">
                                    <td>{{key}}</td>
                                    <td>{{parseFloat(mcqData.time_estimate[key]).toFixed(2)}}s</td>
                                </tr>
                            <!-- </div> -->
                        </tbody>
                    </v-table>
                </v-expansion-panel-text>
            </v-expansion-panel>
            <!-- target -->
            <v-expansion-panel v-if="!no_target">
                <v-expansion-panel-title class="ma-0">
                    <p>Leerdoel</p>
                </v-expansion-panel-title>
                <v-expansion-panel-text >
                    <v-expansion-panels>
                        <OutputListTarget :id="mcqData.target_id" />
                    </v-expansion-panels>
                </v-expansion-panel-text >
            </v-expansion-panel>
        </v-expansion-panels>

    </v-expansion-panel-text>
</v-expansion-panel>
</template>

<script>
import {defineAsyncComponent} from 'vue' 

export default {
    name: 'McqPanel',
    props: {
        id: [Number, String],
        no_target: {type:Boolean, default: false},
    },
    components: {
        OutputListTarget: defineAsyncComponent(() => import('./TargetPanel.vue')) 
    },
    data (){
        return {
            refresh_mcq_data: 0,
            loaded_data: false,
        }
    },
    computed: {
        mcqData (){
            this.refresh_mcq_data
            return this.$store.mcqDataFromId(this.id)
        }
    },
    methods: {
        totalAnswerDistribution: function(answer_distribution){
            var total = 0
            Object.values(answer_distribution).forEach((e) => {
                total = total + Number(e)
            })
            return total
        },
        loadMcqData: async function(){
            if (this.loaded_data == false){
                await this.$store.dispatch('loadMcqsFromArray', [this.id])
                
                this.refresh_mcq_data++
                this.loaded_data = true
            }
        }

    },
}
</script>

<style>
.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    width: 100%;
    font-size: smaller;
    display: inline-block;
}
</style>