<template>
<!-- create a button for every available (depends on data_input) request in veux store query_object-->
<div v-for="(action, index) in availableActions" 
    :key="index" 
    style="padding: 3px" 
    size="h-auto">
    <!-- if the correct selected items, show, else, disabled -->
    <div v-if="(selectedItemsIdsLength == 1 && action.request_input_type != Array)
                || (selectedItemsIdsLength >= 1 && action.request_input_type == Array)"
        size="h-auto">

        <!-- firsts sets the input to the current ids, then creates a request that will output in the veux query_object.response -->
        <v-btn :loading="action.loading" 
                @click="execAction(action)"  
                variant="outlined" 
                :color="buttoncolor">
            <span class="truncate pa-2">{{action.title}}</span>
        </v-btn>
    </div>
    <!-- if the request is disabled (because you maybe selected too many), disable the button -->
    <div v-else>
        <v-btn disabled  
                variant="outlined" 
                :color="buttoncolor" 
                size="h-auto" >
            <span class="truncate pa-2">{{action.title}}</span>
        </v-btn>
    </div>
</div>
</template>

<script>

export default {
    name: 'Requesters',
    props: {
        request_type: String,
    },
    data (){
        return { 
            buttoncolor: '#3F816C', 
        }
    },
    computed: {
        selectedItemIdsOfType (){
            return this.$store.state.datapagina_module.selected_item_ids[this.request_type]
        },
        //returns the length of selected items, used to know wich request is available
        // used in: template
        selectedItemsIdsLength (){
            return this.selectedItemIdsOfType.length
        },
        availableActions(){
            return this.$store.state.datapagina_module.actions.filter((action) => {return action.request_input_data_type == this.request_type && action.shown == true})
        }

    },
    methods: {
        execAction: async function(action){
            this.$store.commit('setActionData', {name: action.name, key: 'loading', value: true})

            var request_input = this.selectedItemIdsOfType
            
            if (action.has_variable) {
                // check the input
                if (action.request_input_type == Number){
                    request_input = Number(request_input[0])
                }

                action.request_input = request_input
            }

            if (action.is_request){

                const query_object = this.createApiRequestObject(action)

                var response = await this.$store.dispatch('apiRequest', {object: query_object}).then((response) => {return response})
            } else {
                var response = object.format_reponse_data(action.request_input)
            }
            
            this.$store.commit('setSelectedTab', object.name)
            this.$store.commit('setActionData', {name: action.name, key: 'response', value: response})
            this.$store.commit('setActionData', {name: action.name, key: 'loading', value: false})

        },
        createApiRequestObject: function(action){
            const query_object = {
                name: action.name,
                path: action.path,
                endpoint: action.endpoint,

                is_data_server: action.is_data_server,

                has_variable: action.has_variable,
                request_input_name: action.request_input_name,
                request_input_type: action.request_input_type,
                request_input: action.request_input,

                format_reponse_data: action.format_reponse_data
            }

            return query_object
        },







        // first set the query data
        // sends the query obj to the api
        // sumbit obj saves the reponse in itself
        // used in: template
        sendRequest: function(query_object){
            this.$store.commit('setQueryData', {'obj':query_object,
                                                'name': 'value', 
                                                'value': this.$store.state.datapagina_module.selected_item_ids[this.request_type]})
            this.$store.dispatch('submitObj', query_object)
        },

    },
    created(){

        // // actions that are of the same type as the request_type
        // this.available_actions = this.actions.filter((action) => {return action.request_input_data_type == this.request_type && action.shown == true})

    },

}
</script>

