<template>

<!-- status -->
<div>
    <!-- if a action_obj is sending, display loading, if nothing is sending and nothing recieved: you did not send request, if not sending and has response: display action_obj that has a response title -->
    <p v-if="(action_obj.loading == true)">Loading ...</p>
    <p v-else-if="(action_obj.response.length == 0)">Did not send request</p>
    <p v-else>success!</p>
</div>
<!-- data -->
<div>
    Verstuurd: <input :value="action_obj.send_ids" readonly/>
</div>
<!-- repsonse list -->
<div>
    <v-expansion-panels class="scrollable list" style="height: 55vh;" variant="accordion"  multiple>
        <v-row>
            <v-col>
                <div v-if="(action_obj.response.length !== 0)">
                    <!-- for every returned id, create a list item that handles descriptions -->
                    <!-- if the response is a target -->
                    <div v-for="(id , index) in action_obj.response" :key="index">
                        <div v-if="action_obj.returns_data_type === 'target'">
                                <TargetPanel :id="id"  style="width: 100%" />

                        </div>
                        <!-- if the response is a mcq -->
                        <div v-if="action_obj.returns_data_type == 'mcq'">
                                <McqPanel :id="id"  />
                        </div>
                        <!-- <hr noshade="true" size="2px"> -->
                    </div>
                </div>
            </v-col>
        </v-row>
        

    </v-expansion-panels>
    <v-row>
            <v-sheet class="pa-0 my-4 ml-4" width="250px">
                <v-text-field hide-details :model-value="action_obj.response" variant="outlined" readonly class="pa-0"></v-text-field>
            </v-sheet>
            <v-sheet class="pa-2 my-4">
                <v-btn @click="copyText(action_obj.response)"  >Copy ids</v-btn>

            </v-sheet>
    </v-row>
</div>
</template>

<script>
import TargetPanel from '@/components/global/TargetPanel'
import McqPanel from '@/components/global/McqPanel'

export default {
    name: 'QueryOutputTab',
    components: {
        TargetPanel,
        McqPanel,
    },
    props: {
        action_obj: Object,
    },
    data (){
        return {

        }
    },
    computed: {
        
    },
    methods: {
        // used for the copy ids section
        // used in: template
        copyText(text) {  
            const el = document.createElement('textarea');  
            el.value = text;                    
            el.setAttribute('readonly', '');                
            el.style.position = 'absolute';                     
            el.style.left = '-9999px';                      
            document.body.appendChild(el);                  
            const selected =  document.getSelection().rangeCount > 0  ? document.getSelection().getRangeAt(0) : false;                                    
            el.select();                                    
            document.execCommand('copy');                   
            document.body.removeChild(el);                  
            if (selected) {                                 
            document.getSelection().removeAllRanges();    
            document.getSelection().addRange(selected);   
            }
        },
    },
    watch: {
    },
}
</script>

<style>
.scrollable{
  overflow-y: scroll;
}
.list{
  border: 1px solid steelblue;
  padding: 5px;
  margin: 2px;  
}
.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    font-size: smaller;
}
</style>